const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			list: [],
			currentIndex: 0,
			mask:false,
			moneyList: [
				{name:'所有额度'},
				{name:'100-5000'},
				{name:'5000-2万'},
				{name:'2万-5万'},
				{name:'5万-10万'},
				{name:'10万以上'}
			]
		};
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		// this.getList()
	},
	methods: {
		getTap(e){
			console.log(e)
			this.mask = true
		},
		screenList(e){
			console.log(e)
			this.currentIndex = e
		},
		hideModal(){
			this.mask = false
		},
		//获取列表数据
		getList() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories-holes`,
				method: 'GET',
				// header: {
				// 	authorization: app.globalData.token
				// },
				data: {
					category_id: this.category_id,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
